/* required file for gridstack to work */
@import "https://cdnjs.cloudflare.com/ajax/libs/gridstack.js/5.0.0/gridstack.min.css";

@import url("https://fonts.googleapis.com/css?family=Baloo+Da&display=swap");
:root {
  --zone-bg-peach: #cc6d6d;
  --zone-bg-purple: #611caf;
}

/* Optional styles for demos */
.btn-primary {
  color: #fff;
  background-color: #007bff;
}

.btn {
  display: inline-block;
  padding: 0.375rem 0.75rem;
  line-height: 1.5;
  border-radius: 0.25rem;
}

a {
  text-decoration: none;
}

h1 {
  font-size: 2.5rem;
  margin-bottom: 0.5rem;
}

.grid-stack {
  background: #fafad2;
}

.grid-stack-item-content {
  color: #2c3e50;
  text-align: center;
  background-color: #18bc9c;
}

.grid-stack-item-removing {
  opacity: 0.5;
}
.trash {
  height: 100px;
  background: rgba(255, 0, 0, 0.1) center center
    url(data:image/svg+xml;utf8;base64,)
    no-repeat;
}
.sidebar {
  background: rgba(0, 255, 0, 0.1);
  padding: 25px 0;
  height: 100px;
  text-align: center;
}
.sidebar .grid-stack-item {
  width: 120px;

  /*Added by me*/
  min-width: 50px;

  height: 50px;
  border: 2px dashed green;
  text-align: center;
  line-height: 35px;
  z-index: 10;
  background: rgba(0, 255, 0, 0.1);
  cursor: default;
  display: inline-block;
}
.sidebar .grid-stack-item .grid-stack-item-content {
  background: none;
}

@media all and (max-width: 767px) {
  .nomelacontainer {
    max-width: 100%;
    background: var(--zone-bg-purple);
  }
}

@media all and (min-width: 768px) {
  .nomelacontainer {
    max-width: 768px;
    background: var(--zone-bg-peach);
    margin-left: auto;
    margin-right: auto;
  }
}

.grid-stack-item-content {
  border: 1px solid grey;
  border-radius: 3px;
  padding: 15px;
}
