@import url(https://cdnjs.cloudflare.com/ajax/libs/gridstack.js/5.0.0/gridstack.min.css);
@import url(https://fonts.googleapis.com/css?family=Baloo+Da&display=swap);
@import url(https://cdn.jsdelivr.net/npm/boxicons@2.0.5/css/boxicons.min.css);
/* required file for gridstack to work */
:root {
  --zone-bg-peach: #cc6d6d;
  --zone-bg-purple: #611caf;
}

/* Optional styles for demos */
.btn-primary {
  color: #fff;
  background-color: #007bff;
}

.btn {
  display: inline-block;
  padding: 0.375rem 0.75rem;
  line-height: 1.5;
  border-radius: 0.25rem;
}

a {
  text-decoration: none;
}

h1 {
  font-size: 2.5rem;
  margin-bottom: 0.5rem;
}

.grid-stack {
  background: #fafad2;
}

.grid-stack-item-content {
  color: #2c3e50;
  text-align: center;
  background-color: #18bc9c;
}

.grid-stack-item-removing {
  opacity: 0.5;
}
.trash {
  height: 100px;
  background: rgba(255, 0, 0, 0.1) center center
    url(data:image/svg+xml;utf8;base64,)
    no-repeat;
}
.sidebar {
  background: rgba(0, 255, 0, 0.1);
  padding: 25px 0;
  height: 100px;
  text-align: center;
}
.sidebar .grid-stack-item {
  width: 120px;

  /*Added by me*/
  min-width: 50px;

  height: 50px;
  border: 2px dashed green;
  text-align: center;
  line-height: 35px;
  z-index: 10;
  background: rgba(0, 255, 0, 0.1);
  cursor: default;
  display: inline-block;
}
.sidebar .grid-stack-item .grid-stack-item-content {
  background: none;
}

@media all and (max-width: 767px) {
  .nomelacontainer {
    max-width: 100%;
    background: #611caf;
    background: var(--zone-bg-purple);
  }
}

@media all and (min-width: 768px) {
  .nomelacontainer {
    max-width: 768px;
    background: #cc6d6d;
    background: var(--zone-bg-peach);
    margin-left: auto;
    margin-right: auto;
  }
}

.grid-stack-item-content {
  border: 1px solid grey;
  border-radius: 3px;
  padding: 15px;
}

body {
    --primary: 25,91,255;
    --color: 44, 62, 80;
    align-items: flex-start;
    /*
    display: flex;
    justify-content: center;
    */
    height: 100vh;
    background: #f4f7f8;
    height: calc(1vh * 100);
    height: calc(var(--vh, 1vh) * 100);
    overflow: hidden;
    color: rgb(var(--color));
    width: 100%;
}
* {
    list-style: none;
    outline: none;
    padding: 0;
    margin: 0;
    font-family: 'Poppins', sans-serif;
    box-sizing: border-box;
}
.container {
    position: fixed;
    top: calc(1vh * 100);
    top: calc(var(--vh, 1vh) * 100);
    width: 100%;
    height: calc(1vh * 50);
    height: calc(var(--vh, 1vh) * 50);
    background: #fff;
    border-radius: 30px 30px 0px 0px;
    padding-top: 40px;
    box-sizing: border-box;
    box-shadow: 0px 0px 30px 0px rgba(0,0,0,.05);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.container::after {
    content: '';
    left: 50%;
    top: -14px;
    width: 45px;
    height: 4px;
    background: rgba(0,0,0,.2);
    position: absolute;
    -webkit-transform: translate(-50%);
            transform: translate(-50%);
    border-radius: 20px;
}
.container p {
    width: 70%;
    padding-top: 20px;
    font-size: .8rem;
    text-align: center;
    font-weight: 500;
    opacity: .7;
}
.container img {
    width: 75%;
}
.container button {
    padding: 12px 30px;
    background: rgb(232, 76, 79);
    color: #fff;
    border: 0px;
    margin-top: 20px;
    border-radius: 17px;
}
.container::before {
    content: '';
    left: 0px;
    bottom: -150px;
    width: 100%;
    height: 150px;
    background: #fff;
    position: absolute;
}
.bottom-navbar {
    position: fixed;
    bottom: 0px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    width: 100%;
    background: #fff;
    border-radius: 30px 30px 0px 0px;
    padding: 10px 0px;
    box-shadow: 0px 0px 30px 0px rgba(0,0,0,.05);
}
.bottom-navbar button {
    width: 60px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 0px;
    background: transparent;
    border-radius: 20px;
    transition: all .25s ease;
}
.bottom-navbar button:active:not(.float) {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}
.bottom-navbar button.float {
    margin-top: -50px;
    background: rgb(232, 76, 79);
    color: #fff;
    border-radius: 25px;
    height: 60px;
    box-shadow: 0px 10px 20px 0px rgba(232, 76, 79, .4);
}
.bottom-navbar button.active {
    color: rgb(232, 76, 79);
}
.bottom-navbar button i {
    font-size: 2rem;
    pointer-events: none;
}
.con-effect {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0px;
    left: 0px;
    overflow: hidden;
    pointer-events: none;
    display: flex;
    align-items: center;
    justify-content: center;
}
.effect {
    background: rgba(232, 76, 79, .15);
    width: 60px;
    height: 50px;
    position: absolute;
    left: 13px;
    border-radius: 18px;
}

.bx{
    font-size: 80px;
}
